.access {
  &__title {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 500;
    font-size: 2rem;
    line-height: 37px;
    color: #555555;
    padding-bottom: 10px;
    margin-bottom: 10px;
    border-bottom: 1px solid #d2d2d21f;
  }

  &__body {
      margin-bottom: 2rem;
  }

  &__buttons {
      display: flex;
      align-items: center;
      gap: 1rem;
  }

  &__btn {
    display: block;
    text-decoration: none;
    text-align: center;
  }
}
