.login-phone {
  display: grid;
  grid-auto-columns: 1fr;
  grid-template-rows: 1fr;
  gap: 0px 0px;
  justify-items: center;
  align-items: center;
  padding: 20px 40px;
  background-color: #eff3f8;
  overflow: hidden;
  border-radius: 14px;
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  font-size: 1rem;
  font-weight: 500;
  @media screen and (max-width: 500px) {
    padding: 5px 10px;
  }

  &-logo {
    width: 300px;
    height: 154.08px;
    margin-bottom: 50px;
  }
  &-info {
    margin: 20px 0;
  }
  &-subtitle {
    font-style: normal;
    font-weight: normal;
    font-size: 0.93rem;
    line-height: 17px;
    text-align: center;
    color: #333;
    margin: 20px 0;
  }
  input {
    width: 385px;
    height: 60px;

    text-align-last: left;
    background: #ffffff;
    border-radius: 20px;
    @media screen and (max-width: 500px) {
      width: 100%;
    }
  }
  button {
  }
}
